import React from 'react'
import { ArrowRightIcon, PlusCircleIcon } from '@heroicons/react/24/outline';
const companyStatus = [
    { label: '245%', discp: 'More revenues for the brand' },
    { label: '130K+', discp: 'Audience Reached' },
    { label: '50+', discp: 'Brands trust us' },
    { label: '24+', discp: 'Worldwide Awards' },
    // {
    //   name: 'Invoices',
    //   href: '/dashboard/invoices',
    //   icon: DocumentDuplicateIcon,
    // },
    // { name: 'Customers', href: '/dashboard/customers', icon: UserGroupIcon },
  ];
  
export default function CompanyStatus() {
    return (
        <>
            <div className='grid  px-4 md:px-12 lg:px-24 md:py-4 content-center '>
                <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-2 md:py-10 justify-between lg:gap-28'>
                    <div className='grid grid-cols-1 gap-8 md:gap-x-10 md:gap-y-7 lg:gap-x-20 lg:gap-y-7 md:grid-cols-2 '>
                        {companyStatus.map((status) => {
                            return (
                                <a
                                    key={status.label}
                                    href={status.discp}
                                    className=" items-center justify-center md:px-2  lg:px-5 md:text-xs lg:text-sm font-medium  "
                                >
                                    <p className="text-6xl font-semibold ">{status.label}</p>
                                    <p className="text-base font-medium text-[#00000099]">{status.discp}</p>
                                </a>
                            );
                        })}
                    </div>
                    <div className='grid grid-cols-1 content-center py-12 lg:py-0'>
                        <h1 className='text-4xl md:text-5xl lg:text-6xl font-medium  '>Commitments</h1>
                        <h1 className='text-lg md:text-xl lg:text-lg  font-medium text-[#00000099] pt-5 pb-10 '>We are committed to working with you collaboratively to understand your goals and create a strategy that will achieve them.</h1>
                        <div className="grid grid-cols-1 justify-items-start content-center bg-white">
                            <button className='flex flex-row items-center gap-2 text-[#3461FF]'>Learn more <ArrowRightIcon className='w-5' /></button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
