import React from 'react'
import Image from 'next/image'
import { Button } from '../button'

const ourWork = [
    { id: '1', image: '/ourWork1.jpeg', label: 'Access Control' },
    { id: '2', image: '/ourWork2.jpeg', label: 'Access Control' },
    { id: '3', image: '/ourWork3.jpeg', label: 'Access Control' },
]

export default function OurWork() {
    return (
        <>
            <div className='grid  px-4 md:px-12 lg:px-24 md:py-4 content-center '>
                <div className='grid grid-cols-1  lg:gap-y-1 lg:my-24 '>

                    <div className='flex flex-col  lg:py-0 items-center justify-center'>
                        <h1 className='text-5xl md:text-5xl lg:text-6xl font-medium   '>Our Work</h1>
                        <h1 className='text-sm md:text-md lg:text-lg font-medium text-[#00000099] pt-2 pb-7'>A glimpse of our portfolio</h1>
                    </div>
                    <div className='flex flex-col md:flex-row  justify-between  gap-5  '>
                        {ourWork.map((work) => {
                            return (
                                <div key={work.id} className="">
                                    <Image key={work.id} width={450} height={300} alt='IT Imge' src={work.image} className='md:h-44 h-60  lg:w-72 lg:h-60  rounded-[40px] ' />
                                    <h1 className='text-center text-md font-medium pt-2'>{work.label}</h1>
                                </div>
                            )
                        })}
                    </div>
                    <div className='flex flex-col items-center justify-center py-5'>
                    <Button className='bg-[#3461FF] md:mx-4 px-4 text-sm my-4 md:my-0  text-[#ffffff] mt-20'>See More</Button>
                        {/* <Button className='md:mx-4 px-4 text-sm my-4 md:my-0 bg-[#3461FF]'>See More</Button> */}
                    </div>
                </div>
            </div>
        </>
    )
}
