'use client'
import React from 'react'
import Image from 'next/image';
import { useState, useRef, useEffect } from 'react';

const companyServices = [[
    {
        id: '1',
        heading: 'IT Infrastructure',
        headingDiscp: 'End-to-end solutions for network management, cloud integration, and infrastructure optimization.',
    }
],
[
    {
        id: '2',
        heading: 'Digital Transformation Consulting',
        headingDiscp: 'Helping organizations transition seamlessly into the digital age, boosting efficiency and innovation.',
    }
],
[
    {
        id: '3',
        heading: 'Quality Assurance & Monitoring',
        headingDiscp: 'Reliable surveillance and monitoring to ensure your systems perform optimally, reducing risks and downtime.',
    }
]
];
const companyInfraStructure = [
    {
        id: '1',
        image: '/gray.png',
        label: 'Network Optimization',
        discp: 'Streamlining network performance for reliable connectivity.'
    },
    {
        id: '2',
        image: '/yellow.png',
        label: 'Cloud Integration',
        discp: 'Seamless cloud adoption for enhanced flexibility.'
    },
    {
        id: '3',
        image: '/gray.png',
        label: 'Data Center Solutions',
        discp: 'Efficient data center management and support.'
    },
    {
        id: '4',
        image: '/yellow.png',
        label: 'System Scalability',
        discp: 'Scalable infrastructure to support business growth.'
    },
];

export default function CompanyStructure() {
    const [contentIndex, setContentIndex] = useState(0);
    const [isLocked, setIsLocked] = useState(false);
    // const scrollRef = useRef(null);
    const scrollRef = useRef<HTMLDivElement | null>(null);
    let debounceTimeout = useRef<Timeout | null>(null);

    useEffect(() => {
    const handleScrollLock = () => {
      const section = scrollRef.current;
      if (section) {
        const { top, bottom } = section.getBoundingClientRect();
        
        // Lock scroll only when CompanyStructure is fully in view and content changes are not complete
        if (top < window.innerHeight && bottom > 0 && !isLocked) {
          document.body.style.overflow = 'hidden'; // Lock scroll
          setIsLocked(true);
        }
      }
    };

    const handleScrollUnlock = () => {
      if (contentIndex === 2) {
        document.body.style.overflow = 'auto'; // Unlock scroll after all content is displayed
        setIsLocked(false);
      }
    };

    const handleScroll = () => {
      if (debounceTimeout.current) clearTimeout(debounceTimeout.current);

      debounceTimeout.current = setTimeout(() => {
        handleScrollLock();

        setContentIndex((prevIndex) => {
          if (prevIndex < 2) {
            return prevIndex + 1;
          } else {
            handleScrollUnlock();
            return prevIndex; // Stop incrementing once all content has been shown
          }
        });
      }, 300); // 300ms debounce for smoother scrolling
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
      clearTimeout(debounceTimeout.current); // Clean up debounce timer
      document.body.style.overflow = 'auto'; // Ensure scroll is unlocked on cleanup
    };
  }, [contentIndex, isLocked]);


    return (
        <>
            <div ref={scrollRef} className='grid  px-4 md:px-12 lg:px-24  content-center '>
                <div className='grid grid-cols-1 lg:grid-cols-2 justify-between lg:gap-28'>
                    {companyServices[contentIndex].map((service, index) => {
                        return (
                            <div key={index} className='flex flex-col py-12 lg:py-0'>
                                <h1 className='text-3xl md:text-4xl lg:text-4xl font-medium  '>{service.heading}</h1>
                                <h1 className='text-sm md:text-md lg:text-lg  font-medium text-[#00000099] pt-4 pb-16 '>{service.headingDiscp}</h1>
                                <Image
                                    src="/ITeqpt.jpeg"
                                    width={487}
                                    height={311}
                                    className="lg:w-96 lg:h-56  md:w-96 md:h-56 rounded-[40px] object-cover"
                                    alt="Hero section image"
                                />
                            </div>
                        );
                    })}
                    <div className='grid grid-cols-1 items-center justify-center gap-8 md:gap-x-10 md:gap-y-7 lg:gap-x-20 lg:gap-y-5 md:grid-cols-1'>
                        {companyInfraStructure.map((structure) => {
                            return (
                                <div
                                    key={structure.id}
                                    className=" flex flex-row    "
                                >
                                    <Image width={106} height={106} alt='IT Imge' src={structure.image} className='w-20 h-20 rounded-3xl' />
                                    <div className='flex flex-col ml-4 lg:ml-8 justify-center '>
                                        <p className="text-xs md:text-xl lg:text-lg font-semibold ">{structure.label}</p>
                                        <p className="text-xs md:text-sm lg:text-xs font-medium text-[#00000099] py-3">{structure.discp}</p>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}
